'use client';

import React, { useState } from 'react';
import Image from 'next/image';

const DistrictSearchSection: React.FC = () => {
  // 임시 데이터
  const recentSearches = ['서울시 강남구', '부산시 해운대구', '인천시 연수구'];
  const [searchValue, setSearchValue] = useState('');

  const handleRecentSearchClick = (value: string) => {
    setSearchValue(value);
  };

  return (
    <div className="bg-[#F4F4F4] rounded-[30px] p-16 flex flex-col items-center gap-8">
      <div className="text-2xl font-semibold text-gray-800 text-center">
        229개 지자체중 우리 지역 찾아보기
      </div>

      <p className="text-base text-gray-600 text-center leading-relaxed">
        전국의 지자체 정보를 한눈에 비교해보세요.<br />
        지역별 특성과 현황을 쉽게 파악할 수 있습니다.
      </p>

      <div className="w-full max-w-[600px]">
        <div className="flex items-center bg-white rounded-[50px] px-10 h-[60px]">
          <div className="flex-shrink-0 mr-3">
            <Image
              src="/textinput_icon.png"
              alt="검색"
              width={20}
              height={20}
            />
          </div>
          <input
            type="text"
            placeholder="지자체 검색"
            value={searchValue}
            onChange={(e) => setSearchValue(e.target.value)}
            className="w-full h-full bg-transparent text-base
                       placeholder:text-gray-400 focus:outline-none"
          />
        </div>
      </div>

      <div className="w-full max-w-[600px]">
        <div className="text-sm text-gray-600 mb-2">
          최근 검색어
        </div>
        <div className="flex flex-wrap gap-1">
          {recentSearches.map((search, index) => (
            <button
              key={index}
              onClick={() => handleRecentSearchClick(search)}
              className="text-sm text-gray-800 hover:text-gray-600 transition-colors
                         cursor-pointer"
            >
              {search}
              {index < recentSearches.length - 1 ? ', ' : ''}
            </button>
          ))}
        </div>
      </div>
    </div>
  );
};

export default DistrictSearchSection;
